.container {
    margin: calc(-1.5px * var(--zoom));
    position: relative;

    width: calc(21px * var(--zoom));
    height: calc(21px * var(--zoom));

    background-image: url('../img/grid_box.png');
    background-position: center;
    background-size: cover;

    transition: transform .1s;
}

.container:hover {
    transform: scale(115%);
}

.background {
    height: calc(20px * var(--zoom));
    width: calc(20px * var(--zoom));
}

.icon {
    position: absolute;
    top: calc(2px * var(--zoom));
    left: calc(2px * var(--zoom));

    height: calc(16px * var(--zoom));
    width: calc(16px * var(--zoom));
}

.tooltip {
    color: var(--font-color);
    z-index: 100;
    border: rgb(125, 106, 79) solid calc(1px * var(--zoom));
    border-radius: 0;
    opacity: 0.95;
    background-color: rgb(14, 12, 12);
}

.tooltipArrow {
    display: none;
}

.name {
    font-size: calc(7px * var(--zoom));
}